/*--------------------------------------------------------------
# home Section
--------------------------------------------------------------*/
#home {
    width: 100%;
    height: 100vh;
    background: url("./image/croped.webp") top right no-repeat;
    background-size: cover;
    position: relative;
}

@media (min-width: 992px) {
    #home {
        padding-left: 160px;
    }
}

#home:before {
    content: "";
    background: rgba(255, 255, 255, 0.8);
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    right: 0;
}

#home h1 {
    margin: 0;
    font-size: 64px;
    font-weight: 700;
    line-height: 56px;
    color: #45505b;
}

#home p {
    color: #45505b;
    margin: 15px 0 0 0;
    font-size: 26px;
    font-family: "Poppins", sans-serif;
}

#home p span {
    color: #4caf50;
    letter-spacing: 1px;
}

#home .social-links {
    margin-top: 30px;
}

#home .social-links a {
    font-size: 24px;
    display: inline-block;
    color: #45505b;
    line-height: 1;
    margin-right: 20px;
    transition: 0.3s;
}

#home .social-links a:hover {
    color: #4caf50;
}

@media (max-width: 992px) {
    #home {
        text-align: center;
    }

    #home h1 {
        font-size: 32px;
        line-height: 36px;
    }

    #home p {
        margin-top: 10px;
        font-size: 20px;
        line-height: 24px;
    }
}